/**
 * Normalizes image and file URLs to include the correct base path.
 * If a URL starts with /uploads/, it prepends /web to match the basePath configuration.
 * @param {string} url - The URL to normalize.
 * @returns {string} - The normalized URL.
 */
export function normalizePath(url) {
    if (!url) return "";

    // If it's already an absolute URL (http/https), return as is
    if (url.startsWith("http://") || url.startsWith("https://") || url.startsWith("data:")) {
        return url;
    }

    // If it starts with /uploads/ but not /web/uploads/, prepend /web
    if (url.startsWith("/uploads/") && !url.startsWith("/web/uploads/")) {
        return `/web${url}`;
    }

    // Handle raw filenames (e.g., "123-photo.jpg")
    // If it doesn't start with / and has a common extension, assume it's an upload
    const commonExtensions = ['.jpg', '.jpeg', '.png', '.gif', '.svg', '.webp', '.pdf'];
    if (!url.startsWith("/") && commonExtensions.some(ext => url.toLowerCase().endsWith(ext))) {
        return `/web/uploads/${url}`;
    }

    return url;
}
